@import './variables.css';



.app{
  min-height: 100vh;
  background-color: var(--bcg-color);
  transition: background-color 250ms ease-in-out;
}

.app * {
  color: var(--text-color);
  transition: color 250ms ease-in-out;
  font-family: "Inter", serif;
  font-weight: 400;
  font-style: normal;
}

.app .inter-bold{
  font-weight: 600;
}

.app .accent{
  color: var(--text-accent);
}

.description, .date{
  color: var(--text-color-secondary);
}

h1{
  margin: 0;
}

.option {
  background-color: var(--bcg-button);
  padding: 14px 50px;
  border-radius: 0.6em;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.option, .app .menu{
  transition: background-color 250ms ease-in-out;
}

.option:hover{
  background-color: var(--bcg-button-hover);
}

.app .menu {
  background-color: var(--bcg-button);
  z-index: 5;
}

.app .menu-option {
  font-size: x-large;
  transition: font-size 250ms ease-in-out;
}

.app .menu-option:hover{
  font-size: xx-large;
}


.app .mode:hover{
  animation: spin 6s linear forwards;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app .project img {
  transition: all .5s;
}

.app .project:hover img {
  transform: scale(1.2);
}

