:root {
  /* --bcg-color: #fafafa; */
  /* --bcg-button: #ededed; */
  --bcg-color: #ededed;
  --bcg-button: #fafafa;
  --bcg-button-hover: #e4e4e4;
  --bcg-skill: white;
  --text-color-secondary: rgb(156 163 175);
  --text-accent: #1976D2;
  --toggle-bg: rgb(238, 238, 176);
  --toggle-fg: orange;
}

[data-theme='dark']{
  /* --bcg-color: rgb(18,18,18);
  --bcg-button: #1e1e1e; */
  --bcg-button: rgb(18,18,18);
  --bcg-color: #1e1e1e;
  --bcg-button-hover: #252525;
  --text-color: #fafafa;
  --text-color-secondary: rgb(156 163 175);
  --text-accent: #1976D2;
  --toggle-bg: rgb(101, 153, 238);
  --toggle-fg: rgb(29, 52, 88);
}

[data-theme='light']{
  --bcg-color: #ededed;
  --bcg-button: #fafafa;
  /* --bcg-color: #fafafa;  
  --bcg-button: #ededed; */
  --bcg-button-hover: #e4e4e4;
  --text-color: black;
  --text-color-secondary: rgb(95, 94, 95);
  --toggle-bg: rgb(238, 238, 176);
  --toggle-fg: orange;
}

.inter-regular {
  font-family: "Inter", serif;
  font-weight: 400;
  font-style: normal;
}
.inter-bold {
  font-family: "Inter", serif;
  font-weight: 700;
  font-style: normal;
}

.inter-regular-italic {
  font-family: "Inter", serif;
  font-weight: 400;
  font-style: italic;
}